body {
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: "Mulish", sans-serif;
  font-size: 16px;
  line-height: 1.2;
}

.main {
  flex-grow: 1;
}

html,
body {
  height: 100%;
}

.container {
  box-sizing: border-box;
  width: 1472px;
  max-width: 95%;
  max-width: calc(100% - 128px);
  margin: 0 auto;
}

.half-container {
  box-sizing: border-box;
  width: calc(50% + 736px);
  max-width: 95%;
  max-width: calc(100% - 64px);
  margin-left: auto;
  margin-right: 0;
}

.half-container--invert {
  margin-left: 0;
  margin-right: auto;
}

.half-container__sub-container {
  box-sizing: border-box;
  width: 1472px;
  max-width: 100%;
  max-width: calc(100% - 64px);
  margin-left: auto;
  margin-right: 0;
}

.half-container__sub-container--invert {
  margin-left: 0;
  margin-right: auto;
}

#jGrowl {
  z-index: 999999;
}

*:focus {
  outline: none;
}

.hide {
  display: none;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

body.compensate-for-scrollbar {
  overflow: hidden;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

.site-main {
  width: 100%;
  flex-grow: 1;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

[data-fancybox] {
  cursor: pointer;
}

html {
  --js-scrollbar-width: 0px;
  --js-real-vw: 1vw;
}

.d-n-i {
  display: none !important;
}

@media only screen and (max-width: 1200px) {
  .container {
    max-width: calc(100% - 60px);
  }

  .half-container {
    max-width: calc(100% - 30px);
  }

  .half-container__sub-container {
    max-width: calc(100% - 30px);
  }
}

@media only screen and (max-width: 960px) {
  .container {
    max-width: calc(100% - 32px);
  }

  .half-container {
    max-width: calc(100% - 16px);
  }

  .half-container__sub-container {
    max-width: calc(100% - 16px);
  }
}